@keyframes reverse {
  0% {
      left: 47px;
      width: 40px;
  }
  60% {
      left: 3px;
      width: 80px;
  }
  100% {
      left: 3px;
  }
}
  
@keyframes switch {
  0% {
      left: 3px;
  }
  60% {
      left: 3px;
      width: 80px;
  }
  100% {
      left: 47px;
      width: 40px;
  }
}

.toggle-checkbox {
